import { NgModule } from '@angular/core';
import { Router, RouterModule, Routes } from '@angular/router';

import { MainComponent } from '../../app/main.component';
import { ComponentsResolver } from '@app/core/resolvers/components-resolver';
import { ProductsResolver } from '@app/core/resolvers/products-resolver';
import { ProjectsResolver } from '@app/core/resolvers/projects-resolver';
import { QuestionsResolver } from '@app/core/resolvers/questions-resolver';
import { AnswersResolver } from '@app/core/resolvers/answers-resolver';
import { ProjectsComponent } from '@app/core/chart-components/projects/projects.component';
import { UsersResolver } from '@app/core/resolvers/users-resolver';
import { AuthGuard } from '../authentication/auth.guard';

const routes: Routes = [
  {
    path: '',
    component: MainComponent,
    // canActivate: [AuthGuard],
    children: [
      {
        path: '',
        redirectTo: 'overview',
        pathMatch: 'full'
      },
      {
        path: 'overview',
        loadChildren: 'app/ffufm-feedback-dashboard-features/overview/overview.module#OverviewModule',
        data: {
          pageTitle: 'FFUFM Feedback - Overview'
        }
      },
      {
        path: 'team-leads',
        loadChildren: 'app/ffufm-feedback-dashboard-features/team-leads/team-leads.module#TeamLeadsModule',
        data: {
          pageTitle: 'FFUFM Feedback - Team Leads'
        }
      },
      {
        path: 'project-managers',
        loadChildren: 'app/ffufm-feedback-dashboard-features/project-managers/project-managers.module#ProjectManagersModule',
        data: {
          pageTitle: 'FFUFM Feedback - Project Managers'
        }
      },
      {
        path: 'project-managers/:projectManagerId',
        loadChildren: 'app/ffufm-feedback-dashboard-features/projects-by-project-manager/projects-by-project-manager.module#ProjectsByProjectManagerModule',
        data: {
          pageTitle: 'FFUFM Feedback - Projects by Project Manager'
        }
      },
      {
        path: 'projects',
        loadChildren: 'app/ffufm-feedback-dashboard-features/projects/projects.module#ProjectsModule',
        data: {
          pageTitle: 'FFUFM Feedback - Projects'
        }
      },
      {
        path: 'projects/:projectId',
        loadChildren: 'app/ffufm-feedback-dashboard-features/project-info/project-info.module#ProjectInfoModule',
        data: {
          pageTitle: 'FFUFM Feedback - Projects'
        }
      },
      {
        path: 'questions',
        loadChildren: 'app/ffufm-feedback-dashboard-features/questions/questions.module#QuestionsModule',
        data: {
          pageTitle: 'FFUFM Feedback - Questions'
        }
      },
      {
        path: 'teams',
        loadChildren: 'app/ffufm-feedback-dashboard-features/teams/teams.module#TeamsModule',
        data: {
          pageTitle: 'FFUFM Feedback - Projects by Team'
        }
      },
      {
        path: 'teams/:teamId',
        loadChildren: 'app/ffufm-feedback-dashboard-features/projects-by-team/projects-by-team.module#ProjectsByTeamModule',
        data: {
          pageTitle: 'FFUFM Feedback - Teams'
        }
      },
      {
        path: 'teams/:teamId/questions/:questionId',
        loadChildren: 'app/ffufm-feedback-dashboard-features/projects-by-question/projects-by-question.module#ProjectsByQuestionModule',
        data: {
          pageTitle: 'FFUFM Feedback - Projects By Question'
        }
      },
      {
        path: 'projects-by-team',
        loadChildren: 'app/ffufm-feedback-dashboard-features/projects-by-team/projects-by-team.module#ProjectsByTeamModule',
        data: {
          pageTitle: 'FFUFM Feedback - Projects By Team'
        }
      }
      // {
      //   path: 'project-managers',
      // },
      // {
      //   path: 'questions'
      // }
    ],
    resolve: {
      // components: ComponentsResolver,
      // products: ProductsResolver,
      projects: ProjectsResolver,
      users: UsersResolver,
      questions: QuestionsResolver,
      answers: AnswersResolver
    },
  },
  {path: '**', redirectTo: ''},
  {
    path: '',
    component: ProjectsComponent
  }
];

@NgModule({
  declarations: [],
  imports: [
    RouterModule.forRoot(routes, {
      useHash: true
    })
  ],
  exports: [RouterModule],
  providers: [
    ComponentsResolver,
    ProductsResolver,
    ProjectsResolver,
    UsersResolver,
    QuestionsResolver,
    AnswersResolver
  ]
})

export class AppRoutingModule {
  // Diagnostic only: inspect router configuration
  constructor(router: Router) {
    console.log('Routes: ', JSON.stringify(router.config, undefined, 2));
  }
}



