import { Component, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { Project } from '@app/shared/models/project.model';
import { User } from '@app/shared/models/user.model';
import * as moment from 'moment';
import * as _ from 'lodash';
import { Answer } from '@app/shared/models/answer.model';
import { Question } from '@app/shared/models/question.model';
import { AnswersByProjectComponent } from '@app/core/chart-components/answers-by-project/answers-by-project.component';

@Component({
  selector: 'ffufm-feedback-dashboard-projects-by-user',
  templateUrl: './projects-by-user.component.html',
  styleUrls: ['./projects-by-user.component.css']
})
export class ProjectsByUserComponent implements OnInit {

  query: string;
  projects: Project[];
  user: User;
  projectsByUser: any[];
  billableDaysByProject: any[];
  queryResults: any[];

  count: number = 12;
  startDate = moment(Date.now()).format('YYYY-MM-DD');
  chart: string = 'pie-grid';

  answers: Answer[];
  answersByUser: Answer[];
  questions: Question[];

  constructor(public bsModalRef: BsModalRef,
              public modalService: BsModalService) { }

  ngOnInit() {
    setTimeout(() => {
      this.answersByUser = [];
      this.projectsByUser = [];
      this.billableDaysByProject = [];
      this.queryResults = [];
      // console.log("asd");
      // this.projects = this.bsModalRef.content.projects;
      this.projects = this.bsModalRef.content.projects;
      this.user = this.bsModalRef.content.user;
      this.questions = this.bsModalRef.content.questions;
      this.answers = this.bsModalRef.content.answers;

      this.loadProjectsByUser();
      this.findProjectByDate();
      this.loadAnswersByUser();
      // console.log('questions? ' + this.questions);
    });
  }

  loadAnswersByUser() {
    this.answers.forEach((answer) => {
      // this.answersByUser.push(answer);
      // console.log('answers: ' + answer.project_id);
      this.projectsByUser.forEach((project) => {
        if (answer.project_id == project.id) {
          // console.log('answer: ' + answer + '; project: ');
          this.answersByUser.push(answer);
          console.log(project.id + ' <=> ' + answer.project_id);
        }
        // console.log(project.id + ' => ' + answer.project_id);
      });
    });
    // this.answersByUser.push(answer);
  }

  loadAnswersByProject(project) {
    // console.log(this.answers);
    // this.answers.forEach((answer) => {
    //   // console.log(result.id);
    //   if (answer.project_id == result.id) {
    //     this.questions.forEach((question) => {
    //       if (question.id == answer.question_id.toString()) {
    //         alert(question.content + ' ---> ' + answer.content);
    //       }
    //     })
    //   }
    // })

    this.bsModalRef = this.modalService.show(AnswersByProjectComponent, {class: 'modal-lg', ignoreBackdropClick: true});
    this.bsModalRef.content.answers = this.answers;
    this.bsModalRef.content.questions = this.questions;
    this.bsModalRef.content.project = project;
  }

  loadProjectsByUser() {
    this.projects.forEach(project => {
      if (project.teamLeadId === this.user.id.toString() || project.projectManagerId === this.user.id.toString()) {
        this.projectsByUser.push(project);
        project['value'] = Number(project.billableDays);
        this.queryResults.push(project);
        console.log('load projects by user: ' + project);
      }
    });

    this.queryResults.forEach((query) => {
      console.log('query: ' + JSON.stringify(query));
    })

    // this.queryResults = _.sortBy(this.queryResults, (project: Project) => { return project.billingDate });

    // this.queryResults.forEach(project => {
    //   console.log(project.name + " " + project.billingDate);
    // })

  }

  findProjectByDate() {
    this.queryResults = [];
    // this.billableDaysByProject = [];

    this.queryResults = _.filter(this.projectsByUser, (project: Project) => {
      return project.billingDate <= this.startDate;
    });

    // this.billableDaysByProject = _.filter(this.projectsByUser, (project: Project) => {
    //   return project.billingDate <= this.startDate;
    // });

    this.queryResults = _.sortBy(this.queryResults, (project: Project) => { return project.billingDate }).reverse();
    if (this.count.toString() !== '') {
      this.queryResults = this.queryResults.splice(0, this.count);
    }
  }

  onSelect(event) {
    console.log(event);
  }

  toggleChart(chartType) {
    this.chart = chartType;
  }

}



