import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import { distinctUntilChanged } from 'rxjs/operators';

@Injectable()
export class UiStateStore {
  appLoader: BehaviorSubject<boolean>;

  constructor() {
    this.appLoader = new BehaviorSubject(true);
  }

  getAppLoaderState$(): Observable<boolean> {
    return this.appLoader.asObservable().pipe(distinctUntilChanged());
  }

  setAppLoaderState(state: boolean): void {
    this.appLoader.next(state);
  }

}



