import { Restangular } from 'ngx-restangular';
import { environment } from '../../environments/environment';
import { InjectionToken } from '@angular/core';

export const PROJECTS_API = new InjectionToken<any>('projects.api.restangular.config');

export function RestangularProjectsApiFactory(restangular: Restangular) {
    return restangular.withConfig((RestangularConfigurer) => {
        RestangularConfigurer.setBaseUrl(environment.api.projectsUrl);
    });
}



