import { Injectable } from '@angular/core';
import { Answer } from '@app/shared/models/answer.model';
import { Resolve } from '@angular/router';
import { AnswerService } from '@app/core/services';
import { AnswerStore } from '@app/core/stores';

@Injectable()
export class AnswersResolver implements Resolve<Answer[]> {

  constructor(private answerService: AnswerService,
              private answerStore: AnswerStore) {}

  resolve(): any {
    return new Promise((resolve) => {
      this.answerService.getAnswers()
          .subscribe((answers: Answer[]) => {
            // console.log('answer resolver?');
            this.answerStore.saveAnswers(answers);
            return resolve(answers);
          });
    });
  }
}


