import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';

import { ComponentStore } from '@app/core/stores';
import { ComponentService } from '@app/core/services';

import { Component } from '@app/shared/models/component.model';

@Injectable()
export class ComponentsResolver implements Resolve<Component[]> {

  constructor(private componentService: ComponentService,
              private componentStore: ComponentStore) {
  }

  resolve(): any {
    return new Promise((resolve) => {
      this.componentService.getComponents()
          .subscribe((components: Component[]) => {
            this.componentStore.saveComponents(components);
            return resolve(components);
          });
    });
  }
}



