import { Restangular } from 'ngx-restangular';
import { environment } from '../../environments/environment';
import { InjectionToken } from '@angular/core';

export const PRODUCTS_API = new InjectionToken<any>('products.api.restangular.config');

export function RestangularProductsApiFactory(restangular: Restangular) {
    return restangular.withConfig((RestangularConfigurer) => {
        RestangularConfigurer.setBaseUrl(environment.api.productsUrl);
    });
}



