import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationExtras, ParamMap, Router } from '@angular/router';

import { AuthService } from '../auth.service';

@Component({
  templateUrl: './auth-page.component.html',
  styleUrls: ['../style.css']
})
export class AuthPageComponent implements OnInit {

  constructor(private authService: AuthService,
              private activatedRoute: ActivatedRoute,
              private router: Router) {
  }

  ngOnInit() {
    this.activatedRoute
        .queryParamMap
        .subscribe((paramMap: ParamMap) => {
          const authState = paramMap.get('state');
          const accessToken = paramMap.get('access_token');
          localStorage.setItem('auth.state', authState);
          this.verifyToken(authState, accessToken);
        });
  }

  private verifyToken(authState, accessToken): void {
    if (authState === localStorage.getItem('local.auth.state')) {
      this.authService.getBearerToken(accessToken);
    } else {
      this.handleInvalidStateError();
    }
  }

  private handleInvalidStateError(): void {
    const extras: NavigationExtras = {
      queryParams: {
        error: 'Invalid State',
        message: `The oauth state was missing or invalid.`
      }
    };
    this.router.navigate(['auth-error'], extras);
  }
}



