import { Restangular } from 'ngx-restangular';
import { environment } from '../../environments/environment';
import { InjectionToken } from '@angular/core';

export const COMPONENTS_API = new InjectionToken<any>('components.api.restangular.config');

export function RestangularComponentsApiFactory(restangular: Restangular) {
    return restangular.withConfig((RestangularConfigurer) => {
        RestangularConfigurer.setBaseUrl(environment.api.componentsUrl);
    });
}


