import { environment} from '../../environments/environment';
import { Restangular } from 'ngx-restangular';
import { InjectionToken } from '@angular/core';

export const FEEDBACK_API = new InjectionToken<any>('feedback.api.restangular.config');

export function RestangularFeedbackApiFactory(restangular: Restangular) {
    return restangular.withConfig((RestangularConfigurer) => {
        RestangularConfigurer.setBaseUrl(environment.api.feedbackUrl);
    });
}



