import { Injectable } from '@angular/core';

import { Component } from '@app/shared/models/component.model';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class ComponentStore {

    private components$: BehaviorSubject<Component[]> = new BehaviorSubject([]);

    constructor() { }

    saveComponents(components: Component[]): void {
        this.components$.next(components.map(component => {
            return component;
        }));
    }

    getComponents() {
        return this.components$.asObservable();
    }
}



