import { Inject, Injectable } from '@angular/core';
import { Restangular } from 'ngx-restangular';
import { COMPONENTS_API } from '../../../config/requests/components-api.config';

@Injectable()
export class ComponentService {

    constructor(@Inject(COMPONENTS_API) private restangular: Restangular) { }

    getComponents() {
        return this.restangular.all('components').getList();
    }

    getComponentById(componentId) {
        return this.restangular.one('components', componentId).get();
    }
}


