import { Injectable } from '@angular/core';
import { Question } from '@app/shared/models/question.model';
import { Resolve } from '@angular/router';
import { QuestionService } from '@app/core/services';
import { QuestionStore } from '@app/core/stores';

@Injectable()
export class QuestionsResolver implements Resolve<Question[]> {

  constructor(private questionService: QuestionService,
              private questionStore: QuestionStore) {}

  resolve(): any {
    return new Promise((resolve) => {
      this.questionService.getQuestions()
          .subscribe((questions: Question[]) => {
            // console.log('question resolver?');
            this.questionStore.saveQuestions(questions);
            return resolve(questions);
          });
    });
  }
}


