import { Component, Input, OnInit } from '@angular/core';
import { User } from '@app/shared/models/user.model';
import { Project } from '@app/shared/models/project.model';
import { BsModalRef } from 'ngx-bootstrap';
import * as moment from 'moment';

@Component({
  selector: 'ffufm-feedback-dashboard-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {

  bsModalRef: BsModalRef;
  count = 10;
  startDate = moment(Date.now()).format('YYYY-MM-DD');
  projects: Project[];

  @Input() users: User[];
  tlBillableDays: any[];
  pmBillableDays: any[];

  constructor() { }

  ngOnInit() {
    this.projects = this.bsModalRef.content.projects;
    this.users = this.bsModalRef.content.users;
    console.log('received?', this.users);
  }

  reloadUsers() {
    let projectsArray = this.projects;
    let usersArray = this.users;
    let pmBillableDays = [];
    let tlBillableDays = [];
    // let manipulatedUsers = [];
    // console.log(this.users[0].id);
    usersArray.forEach(function (user) {
      console.log(user.displayName);
      let accumulatedBillableDays = 0;
      let role = '';
      projectsArray.forEach(function (project) {
        if (project.projectManagerId === user.id) {
          accumulatedBillableDays += Number(project.billableDays);
          role = 'pm';
        } else if (project.teamLeadId === user.id) {
          accumulatedBillableDays += Number(project.billableDays);
          role = 'tl';
        }
      });
      if (role === 'pm') {
        pmBillableDays.push({
          'name': user.displayName,
          'value': accumulatedBillableDays,
          'role': role
        });
      } else if (role === 'tl') {
        tlBillableDays.push({
          'name': user.displayName,
          'value': accumulatedBillableDays,
          'role': role
        });
      }
    });
    this.pmBillableDays = pmBillableDays;
    this.tlBillableDays = tlBillableDays;

    console.log(this.tlBillableDays);
    console.log(this.pmBillableDays);
  }

}



