import { Inject, Injectable } from '@angular/core';
import { Restangular } from 'ngx-restangular';
import { FEEDBACK_API } from '../../../config/requests/feedback-api.config';

@Injectable()
export class AnswerService {

  constructor(@Inject(FEEDBACK_API) private restangular: Restangular) { }

  getAnswers() {
    return this.restangular.all('answers').getList();
  }

  postAnswer(answer) {
    this.restangular.all('answers').post(answer);
    return answer;
  }
}


