import { NgModule } from '@angular/core';
import { Restangular, RestangularModule } from 'ngx-restangular';

import { environment } from '../../environments/environment';
import { AuthService } from '../authentication/auth.service';

import { FEEDBACK_API, RestangularFeedbackApiFactory } from './feedback-api.config';
import { PROJECTS_API, RestangularProjectsApiFactory } from './projects-api.config';
import { RestangularUsersApiFactory, USERS_API } from './users-api.config';
import { COMPONENTS_API, RestangularComponentsApiFactory } from './components-api.config';
import { PRODUCTS_API, RestangularProductsApiFactory } from './products-api.config';

export function RestangularConfigFactory(RestangularProvider, authService) {
  RestangularProvider.setPlainByDefault(true);
  RestangularProvider.setRequestSuffix('/');
  RestangularProvider.setBaseUrl(environment.api.feedbackUrl);

  const token = authService.getStoredToken();

  RestangularProvider.setDefaultHeaders({
    // 'Authorization': 'Bearer MTY5OUBjMTEzZjkxNTg4NzhhNTljNzI2MmUxYzQ5NmIyOGQ4ZjU1NjZmNGZmNDQ2OTFjYjgzZWYzZWIxNjA4MWMwNWI3'
    // 'Authorization': token.type + ' ' + token.access_token
  });

  RestangularProvider.addResponseInterceptor((data, operation, what, url, response) => {
    if (operation === 'remove') {
      return {};
    }
    return data;
  });

  RestangularProvider.addErrorInterceptor(response => {
    if (response.status === 401) {
      authService.getAuthorization();
    }
  });
}

@NgModule({
  imports: [
    RestangularModule.forRoot([AuthService], RestangularConfigFactory)
  ],
  declarations: [],
  providers: [
    {provide: COMPONENTS_API, useFactory: RestangularComponentsApiFactory, deps: [Restangular]},
    {provide: FEEDBACK_API, useFactory: RestangularFeedbackApiFactory, deps: [Restangular]},
    {provide: PRODUCTS_API, useFactory: RestangularProductsApiFactory, deps: [Restangular]},
    {provide: PROJECTS_API, useFactory: RestangularProjectsApiFactory, deps: [Restangular]},
    {provide: USERS_API, useFactory: RestangularUsersApiFactory, deps: [Restangular]}
  ]
})

export class AppRequestsModule {
}



