import {Restangular} from 'ngx-restangular';
import {PRODUCTS_API} from '../../../config/requests/products-api.config';
import {Inject, Injectable} from '@angular/core';

@Injectable()
export class ProductService {

    constructor(@Inject(PRODUCTS_API) private restangular: Restangular) { }

    getProducts() {
        return this.restangular.all('products').getList();
    }

    getProductById(productId) {
        return this.restangular.one('products', productId).get();
    }

}


