import { Component, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import * as moment from 'moment';
import * as _ from 'lodash';
import { User } from '@app/shared/models/user.model';
import { Project } from '@app/shared/models/project.model';
import { ProjectsByUserComponent } from '@app/core/chart-components/projects-by-user/projects-by-user.component';
import { Answer } from '@app/shared/models/answer.model';
import { Question } from '@app/shared/models/question.model';

@Component({
  selector: 'ffufm-feedback-dashboard-project-manager',
  templateUrl: './project-manager.component.html',
  styleUrls: ['./project-manager.component.css']
})
export class ProjectManagerComponent implements OnInit {

  query: string;
  queryResults: any[];
  // bsModalRef: BsModalRef;
  count = 10;
  startDate = moment(Date.now()).format('YYYY-MM-DD');
  projects: Project[];
  users: User[];
  // tlBillableDays: any[];
  // pmBillableDays: any[];
  projectManagers: User[];
  answers: Answer[];
  questions: Question[];

  constructor(public bsModalRef: BsModalRef,
              public modalService: BsModalService
  ) { }

  ngOnInit() {
    setTimeout(() => {
      // console.log("project manager component");
      this.projects = this.bsModalRef.content.projects;
      this.users = this.bsModalRef.content.users;
      this.questions = this.bsModalRef.content.questions;
      this.answers = this.bsModalRef.content.answers;
      // this.reloadUsers();
      this.loadProjectManagers();
      // console.log('received?', this.users);
      // console.log('projects? ', this.projects);
    });
  }

  loadProjectsByUser(user) {
    this.bsModalRef = this.modalService.show(ProjectsByUserComponent, {class: 'modal-lg', ignoreBackdropClick: true});
    this.bsModalRef.content.user = user;
    this.bsModalRef.content.projects = this.projects;
    this.bsModalRef.content.questions = this.questions;
    this.bsModalRef.content.answers = this.answers;
  }

  loadProjectManagers() {
    // console.log("load project managers");
    this.projectManagers = this.projects.reduce((acc, project: Project) => {
      const {projectManagerId} = project;
      if (_.findIndex(acc, {id: projectManagerId}) === -1 && projectManagerId !== null) {
        const user = _.find(this.users, {id: projectManagerId});
        acc.push(user);
      }
      return acc;
    }, []);
    this.queryResults = this.projectManagers;
    // this.projectManagers.forEach(person => {console.log(person)});
  }

  findUser(query: string) {
    this.queryResults = [];
    this.projectManagers.forEach(user => {
      if (user.displayName.toLowerCase().includes(query.toLowerCase())) {
        this.queryResults.push(user);
      }
    })
  }
}



