  showModal(type: number) {
    switch (type) {
      case TypeOfModalEnum.project:
        this.bsModalRef = this.modalService.show(ProjectsComponent, {class: 'modal-lg', ignoreBackdropClick: true});
        this.bsModalRef.content.projects = this.projects;
        this.bsModalRef.content.users = this.users;
        this.bsModalRef.content.sortedProjects = this.sortedProjects;
        break;
      case TypeOfModalEnum.teamLead:
        this.bsModalRef = this.modalService.show(TeamLeadComponent, {class: 'modal-lg', ignoreBackdropClick: true});
        this.bsModalRef.content.projects = this.projects;
        this.bsModalRef.content.users = this.users;
        this.bsModalRef.content.sortedProjects = this.sortedProjects;
        this.bsModalRef.content.questions = this.questions;
        this.bsModalRef.content.answers = this.answers;
        break;
      case TypeOfModalEnum.projectManager:
        this.bsModalRef = this.modalService.show(ProjectManagerComponent, {class: 'modal-lg', ignoreBackdropClick: true});
        this.bsModalRef.content.projects = this.projects;
        this.bsModalRef.content.users = this.users;
        this.bsModalRef.content.sortedProjects = this.sortedProjects;
        this.bsModalRef.content.questions = this.questions;
        this.bsModalRef.content.answers = this.answers;
        break;
      case TypeOfModalEnum.question:
        this.bsModalRef = this.modalService.show(QuestionComponent, {class: 'modal-lg', ignoreBackdropClick: true});
        this.bsModalRef.content.questions = this.questions;
        break;
      case TypeOfModalEnum.answer:
        this.bsModalRef = this.modalService.show(AnswerComponent, {class: 'modal-lg', ignoreBackdropClick: true});
        this.bsModalRef.content.answers = this.answers;
        break;
    }
  }

// );
//
//     this.sortedProjects = _.sortBy(this.sortedProjects, (project: Project) => { return project.billingDate }).reverse();
//     this.sortedProjects = this.sortedProjects.splice(0, this.count);
