  reloadProjects() {
    this.sortedProjects = _.map(this.sortedProjects, (project: Project) => {
      project.billingDate = moment(project.billingDate).format('YYYY-MM-DD');
      // console.log(project.billingDate + " /// " + this.startDate);
      return project;
    });

    this.sortedProjects = _.filter(this.projects, (project: Project) => {
      return project.billingDate <= this.startDate;
    });

    this.sortedProjects = _.sortBy(this.sortedProjects, (project: Project) => { return project.billingDate }).reverse();
    this.sortedProjects = this.sortedProjects.splice(0, this.count);
  }

  reloadUsers() {
    /*
    TODO use CONST instead of LET
     */
    let projectsArray = this.projects;
    let usersArray = this.users;
    let pmBillableDays = [];
    let tlBillableDays = [];
    // let manipulatedUsers = [];
    // console.log(this.users[0].id);
    usersArray.forEach(function (user) {
      // console.log(user.displayName);
      let accumulatedBillableDays = 0;
      let role = '';
      projectsArray.forEach(function (project) {
        if (project.projectManagerId === user.id) {
          accumulatedBillableDays += Number(project.billableDays);
          role = 'pm';
        } else if (project.teamLeadId === user.id) {
          accumulatedBillableDays += Number(project.billableDays);
          role = 'tl';
        }
      });
      if (role === 'pm') {
        pmBillableDays.push({
          'name': user.displayName,
          'value': accumulatedBillableDays,
          'role': role
        });
      } else if (role === 'tl') {
        tlBillableDays.push({
          'name': user.displayName,
          'value': accumulatedBillableDays,
          'role': role
        });
      }
    });
    this.pmBillableDays = pmBillableDays;
    this.tlBillableDays = tlBillableDays;

    // console.log(this.tlBillableDays);
    // console.log(this.pmBillableDays);
  }

  private initializeCharts() {
    this.chart.pie = false;
    this.chart.bar = true;
    this.chart.gauge = false;
  }

  private getProjects() {

    this.projectStore.getProjects()
        .subscribe((projects: Project[]) => {
          this.projects = _.map(projects, (project: any) => {
            project['value'] = project.billableDays;
            return project;
            // if (project.teamLeadId === '14') {
            //   console.log('123' + project.name)
            // }
            // return {
            //   'name': project.name,
            //   'value': project.billableDays,
            //   'projectManagerId': project.projectManagerId,
            //   'teamLeadId': project.teamLeadId,
            //   'billingDate': project.billingDate,
            //   'billableDays': project.billableDays,
            //   'id': project.id
            // };
          });
          this.sortedProjects = _.sortBy(this.projects, function (o) { return o.billingDate });
        });
  }

  private getUsers() {
    this.userStore.getUsers()
        .subscribe((users: User[]) => {
          this.users = _.map(users, (user: User) => {
            // return {
            //   'displayName': user.displayName,
            //   'id': user.id
            // }
            return user;
          });
          this.users = _.orderBy(this.users, 'displayName', 'asc');
        });
  }

  private getQuestions() {
    this.questionStore.getQuestions()
        .subscribe((questions: Question[]) => {
          this.questions = questions;
          //   _.map(questions, (question: Question) => {
          //   return question;
          // })
        });
  }

  private getAnswers() {
    this.answerStore.getAnswers()
        .subscribe((answers: Answer[]) => {
          this.answers = answers;
          //   _.map(questions, (question: Question) => {
          //   return question;
          // })
        })
  }

  private generateAnswer() {
    const answer = {
      "content": this.answer_content,
      "project_id": this.answer_project_id,
      "question_id": this.answer_question_id,
      "user_id": 1
    };

    alert(this.answerService.postAnswer(answer) + 'sent');
  }

  //

  //
  // private getProducts() {
  //   this.productStore.getProducts()
  //       .subscribe((products: Product[]) => {
  //         this.products = products;
  //         console.log(this.products);
  //       })
  // }

  // private getComponents() {
  //   this.componentStore.getComponents()
