import { Component, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import * as _ from 'lodash';
import { User } from '@app/shared/models/user.model';
import { Project } from '@app/shared/models/project.model';
import { ProjectsByUserComponent } from '@app/core/chart-components/projects-by-user/projects-by-user.component';
import { Answer } from '@app/shared/models/answer.model';
import { Question } from '@app/shared/models/question.model';
import { QuestionsByUserComponent } from '@app/core/chart-components/questions-by-user/questions-by-user.component';
import { AnswersByUserComponent } from '@app/core/chart-components/answers-by-user/answers-by-user.component';

@Component({
  selector: 'ffufm-feedback-dashboard-team-lead',
  templateUrl: './team-lead.component.html',
  styleUrls: ['./team-lead.component.css']
})
export class TeamLeadComponent implements OnInit {

  query: string;
  queryResults: any[];
  projects: Project[];
  users: User[];
  teamLeads: User[];
  answers: Answer[];
  questions: Question[];

  // answersByUser: any[];

  constructor(public bsModalRef: BsModalRef,
              public modalService: BsModalService
  ) { }

  ngOnInit() {
    setTimeout(() => {
      // console.log("team lead component");
      this.projects = this.bsModalRef.content.projects;
      this.users = this.bsModalRef.content.users;
      this.questions = this.bsModalRef.content.questions;
      this.answers = this.bsModalRef.content.answers;
      // this.answersByUser = [];
      // this.reloadUsers();
      this.loadTeamLeads();
      console.log('received?', this.answers);
      // console.log('projects? ', this.projects);
    });
  }

  loadAnswersByUser(user) {
    this.bsModalRef = this.modalService.show(AnswersByUserComponent, {class: 'modal-lg', ignoreBackdropClick: true});
    this.bsModalRef.content.user = user;
    this.bsModalRef.content.projects = this.projects;
    this.bsModalRef.content.questions = this.questions;
    this.bsModalRef.content.answers = this.answers;

    // loadAnswersByUser() {
    //   this.answers.forEach((answer) => {
    //     // this.answersByUser.push(answer);
    //     // console.log('answers: ' + answer.project_id);
    //     this.projectsByUser.forEach((project) => {
    //       if (answer.project_id == project.id) {
    //         // console.log('answer: ' + answer + '; project: ');
    //         this.answersByUser.push(answer);
    //
    //         this.answersByUser.push({
    //           'name': project.name,
    //           'value': answer.content,
    //           'question_id': answer.question_id,
    //           'project_id': project.id,
    //         });
    //         console.log(project.id + ' <=> ' + answer.project_id);
    //       }
    //       // console.log(project.id + ' => ' + answer.project_id);
    //     });
    //   });
    // this.answersByUser.push(answer);
    // }
  }

  loadQuestionsByUser(user) {
    this.bsModalRef = this.modalService.show(QuestionsByUserComponent, {class: 'modal-lg', ignoreBackdropClick: true});
    this.bsModalRef.content.user = user;
    this.bsModalRef.content.projects = this.projects;
    this.bsModalRef.content.questions = this.questions;
    this.bsModalRef.content.answers = this.answers;

  }

  loadProjectsByUser(user) {
    this.bsModalRef = this.modalService.show(ProjectsByUserComponent, {class: 'modal-lg', ignoreBackdropClick: true});
    this.bsModalRef.content.user = user;
    this.bsModalRef.content.projects = this.projects;
    this.bsModalRef.content.questions = this.questions;
    this.bsModalRef.content.answers = this.answers;
  }

  loadTeamLeads() {
    // console.log("load team leads");
    this.teamLeads = this.projects.reduce((acc, project: Project) => {
      const {teamLeadId} = project;
      if (_.findIndex(acc, {id: teamLeadId}) === -1 && teamLeadId !== null) {
        const user = _.find(this.users, {id: teamLeadId});
        acc.push(user);
      }
      return acc;
    }, []);
    this.queryResults = this.teamLeads;
    this.queryResults = _.sortBy(this.queryResults, (user: User) => { return user })
    // this.teamLeads.forEach(person => {console.log(person)});
  }

  findUser(query: string) {
    this.queryResults = [];
    this.teamLeads.forEach(user => {
      if (user.displayName.toLowerCase().includes(query.toLowerCase())) {
        this.queryResults.push(user);
      }
    })
  }

  showUser(user: User) {
    this.projects.forEach(project => {
      if (user.id.toString() === project.teamLeadId) {
      }
    })
  }

}



