import { NgModule, Optional, SkipSelf } from '@angular/core';
import { CommonModule } from '@angular/common';

import * as fromServices from './services';
import * as fromStores from './stores';

import { UiStateStore } from './ui-state.store';
import { UtilsModule } from './utils/utils.module';
import { UsersComponent } from './chart-components/users/users.component';
import { ProjectsComponent } from './chart-components/projects/projects.component';
import { TeamLeadComponent } from './chart-components/team-lead/team-lead.component';
import { ProjectManagerComponent } from './chart-components/project-manager/project-manager.component';
import { SharedModule } from '@app/shared';
import { FormsModule } from '@angular/forms';
import { ProjectsByUserComponent } from '@app/core/chart-components/projects-by-user/projects-by-user.component';
import { QuestionComponent } from '@app/core/chart-components/question/question.component';
import { AnswerComponent } from '@app/core/chart-components/answer/answer.component';
import { AnswersByProjectComponent } from '@app/core/chart-components/answers-by-project/answers-by-project.component';
import { QuestionsByUserComponent } from '@app/core/chart-components/questions-by-user/questions-by-user.component';
import { AnswersByUserComponent } from './chart-components/answers-by-user/answers-by-user.component';

@NgModule({
  declarations: [
    AnswerComponent,
    AnswersByProjectComponent,
    AnswersByUserComponent,
    QuestionComponent,
    QuestionsByUserComponent,
    ProjectsByUserComponent,
    ProjectsComponent,
    ProjectManagerComponent,
    TeamLeadComponent,
    UsersComponent
  ],
  entryComponents: [
    AnswerComponent,
    AnswersByProjectComponent,
    AnswersByUserComponent,
    QuestionComponent,
    QuestionsByUserComponent,
    ProjectManagerComponent,
    ProjectsByUserComponent,
    ProjectsComponent,
    TeamLeadComponent
  ],
  exports: [
    AnswerComponent,
    AnswersByProjectComponent,
    AnswersByUserComponent,
    QuestionComponent,
    QuestionsByUserComponent,
    ProjectsByUserComponent,
    ProjectsComponent,
    UsersComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    SharedModule,
    UtilsModule
  ],
  providers: [
    ...fromServices.services,
    ...fromStores.stores,
    UiStateStore
  ]
})
export class CoreModule {
  constructor(@Optional() @SkipSelf() parentModule: CoreModule) {
    if (parentModule) {
      throw new Error('CoreModule is already loaded. Import it in the main module only.');
    }
  }
}



