import { Restangular } from 'ngx-restangular';
import { environment } from '../../environments/environment';
import { InjectionToken } from '@angular/core';

export const USERS_API = new InjectionToken<any>('users.api.restangular.config');

export function RestangularUsersApiFactory(restangular: Restangular) {
    return restangular.withConfig((RestangularConfigurer) => {
        RestangularConfigurer.setBaseUrl(environment.api.usersUrl);
    });
}



