import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { Answer } from '@app/shared/models/answer.model';
import { Project } from '@app/shared/models/project.model';
import { Question } from '@app/shared/models/question.model';

@Component({
  selector: 'ffufm-feedback-dashboard-answers-by-project',
  templateUrl: './answers-by-project.component.html',
  styleUrls: ['./answers-by-project.component.css']
})
export class AnswersByProjectComponent implements OnInit {

  answers: Answer[];
  answersByProject: Answer[];
  project: Project;
  questions: Question[];

  constructor(public bsModalRef: BsModalRef) { }

  ngOnInit() {
    setTimeout(() => {
      this.answers = this.bsModalRef.content.answers;
      this.project = this.bsModalRef.content.project;
      this.questions = this.bsModalRef.content.questions;
      this.loadAnswersByProject();
    });
  }

  loadAnswersByProject() {
    this.answersByProject = [];
    this.answers.forEach((answer) => {
      console.log('answers by project: ' + answer.content);
      if (answer.project_id.toString() === this.project.id) {
        this.answersByProject.push(answer);
      }
    })

  }

}



