import { Inject, Injectable } from '@angular/core';
import { FEEDBACK_API } from '../../../config/requests/feedback-api.config';
import { Restangular } from 'ngx-restangular';

@Injectable()
export class FeedbackService {

    constructor(@Inject(FEEDBACK_API) private restangular: Restangular) { }

    getQuestions() {
        return this.restangular.all('questions').getList();
    }

    getChoices() {
        return this.restangular.all('choice').getList();
    }
}


