import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { filter, map, mergeMap } from 'rxjs/operators';

import { UiStateStore } from './core/ui-state.store';

@Component({
  selector: 'app-root',
  template: `
    <app-loader *ngIf="appLoaderState | async"></app-loader>
    <router-outlet></router-outlet>
  `
})

export class AppComponent implements OnInit {
  appLoaderState: Observable<boolean>;

  constructor(private router: Router,
              private activatedRoute: ActivatedRoute,
              private titleService: Title,
              private uiStateStore: UiStateStore) {
    this.appLoaderState = uiStateStore.getAppLoaderState$();
  }

  ngOnInit() {
    this.setPageTitle();
  }

  setPageTitle(): void {
    this.router.events
        .pipe(filter(event => event instanceof NavigationEnd),
          map(() => this.activatedRoute),
          map(route => {
            while (route.firstChild) {
              route = route.firstChild;
            }
            return route;
          }),
          filter(route => route.outlet === 'primary'),
          mergeMap(route => route.data),
          filter(data => typeof data['pageTitle'] !== 'undefined')
        ).subscribe(data => this.titleService.setTitle(data['pageTitle']));
  }
}



