import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {Location} from '@angular/common';
import {NavigationExtras, Router} from '@angular/router';
import 'rxjs/add/operator/map';

import {environment} from '../../environments/environment';
import {APP_CONFIG} from '../settings';
import {BearerToken} from './bearer-token.model';

@Injectable()
export class AuthService {
    appToken: BearerToken;

    constructor(private location: Location,
                private httpClient: HttpClient,
                private router: Router) {
        this.appToken = JSON.parse(localStorage.getItem(APP_CONFIG.localStorageTokenKey));
    }

    isAuthorized(): boolean {
        return Boolean(this.appToken);
    }

    getAuthorization(): void {
        const redirectURI = encodeURIComponent(window.location.origin + window.location.pathname +
            this.location.prepareExternalUrl('authorize'));
        localStorage.setItem('local.auth.state', environment.auth.authState);
        window.location.href = this.getAuthorizationURI(redirectURI);
    }

    getBearerToken(accessToken: string) {
        if (APP_CONFIG.auth === 'pass3') {
            const bearerToken = new BearerToken();
            bearerToken.access_token = accessToken;
            this.setToken(bearerToken);
        } else {
            const httpOptions = {
                headers: new HttpHeaders({
                    'Content-Type': 'application/x-www-form-urlencoded'
                })
            };
            const httpParams = {
                params: new HttpParams()
                    .set('grant_type', environment.auth.grantType)
                    .set('client_id', environment.auth.clientId)
                    .set('client_secret', accessToken)
            };
            this.httpClient.post(environment.auth.tokenEndPoint, httpParams.params, httpOptions)
                .subscribe((token: BearerToken) => {
                        this.setToken(token);
                    },
                    err => {
                        this.handleAuthError(err);
                    }
                );
        }
    }

    getStoredToken(): BearerToken {
        return this.appToken;
    }

    logout(): void {
        localStorage.removeItem(APP_CONFIG.localStorageTokenKey);
        this.appToken = null;
    }

    handleAuthError(err) {
        const extras: NavigationExtras = {
            queryParams: {
                'message': err.message,
                'code': err.code
            }
        };
        this.router.navigate(['auth-error'], extras);
    }

    private setToken(token: BearerToken): void {
        localStorage.setItem(APP_CONFIG.localStorageTokenKey, JSON.stringify(token));
        this.appToken = token;
        this.router.navigate(['']);
    }

    private getAuthorizationURI(redirectURI: string): string {
        return environment.auth.authorizationUrl + '?state=' + environment.auth.authState +
            '&response_type=token&client_id=' + environment.auth.clientId + '&redirect_uri=' + redirectURI;
    }

}



