import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';

import { AuthPageComponent } from './auth-page/auth-page.component';
import { SignOutPageComponent } from './sign-out-page/sign-out-page.component';
import { AuthErrorPageComponent } from './auth-error-page/auth-error-page.component';
import { AuthRoutingModule } from './auth-routing.module';
import { AuthGuard } from './auth.guard';
import { AuthService } from './auth.service';

@NgModule({
  declarations: [
    AuthPageComponent,
    SignOutPageComponent,
    AuthErrorPageComponent
  ],
  imports: [CommonModule, AuthRoutingModule, HttpClientModule],
  providers: [
    AuthService,
    AuthGuard
  ]
})

export class AppAuthModule {

}



