import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap';
import { Project } from '@app/shared/models/project.model';
import { User } from '@app/shared/models/user.model';
import * as _ from 'lodash';
import * as moment from 'moment';

@Component({
  selector: 'ffufm-feedback-dashboard-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.css']
})
export class ProjectsComponent implements OnInit {

  count = 10;
  startDate = moment(Date.now()).format('YYYY-MM-DD');
  projects: Project[];
  users: User[];
  sortedProjects: any[];
  // projects: any[];
  // sortedProjects: any[];

  constructor(public bsModalRef: BsModalRef) { }

  ngOnInit() {
    setTimeout(() => {
      this.sortedProjects = this.bsModalRef.content.sortedProjects;
      this.projects = this.bsModalRef.content.projects;
      this.users = this.bsModalRef.content.users;
      // console.log('received?', this.bsModalRef.content.projects);
    });

  }

  reloadProjects() {
    this.sortedProjects = _.map(this.sortedProjects, (project: Project) => {
      project.billingDate = moment(project.billingDate).format('YYYY-MM-DD');
      // console.log(project.billingDate + " /// " + this.startDate);
      return project;
    });

    this.sortedProjects = _.filter(this.projects, (project: Project) => {
      return project.billingDate <= this.startDate;
    });

    this.sortedProjects = _.sortBy(this.sortedProjects, (project: Project) => { return project.billingDate }).reverse();
    this.sortedProjects = this.sortedProjects.splice(0, this.count);

    // this.sortedProjects.forEach(project => {
    //   console.log(project);
    // })
    console.log(this.sortedProjects);
  }

  select(event) {
    console.log(event);
  }

}



