import { Injectable } from '@angular/core';
import { Project } from '@app/shared/models/project.model';
import { Resolve } from '@angular/router';
import { ProjectService } from '@app/core/services';
import { ProjectStore } from '@app/core/stores';

@Injectable()
export class ProjectsResolver implements Resolve<Project[]> {

  constructor(private projectService: ProjectService,
              private projectStore: ProjectStore) {}

  resolve(): any {
    return new Promise((resolve) => {
      this.projectService.getProjects()
          .subscribe((projects: Project[]) => {
            // console.log('project resolver?');
            this.projectStore.saveProjects(projects);
            return resolve(projects);
          });
    });
  }
}



